<template>
    <div class="departure">
        <p>
            <span v-if="!departureChanged" class="plannedDateTime">{{ showDepartureTime(trainPass.plannedDateTime) }} </span>
            <span v-else>
                <span class="changedTime">{{ showDepartureTime(trainPass.actualDateTime) }} </span>
                <span class="oldTime">{{ showDepartureTime(trainPass.actualDateTime) }} </span>
            </span>
            <span class="lineProduct" :class="[ trainPass.product.categoryCode ]">
                <span>
                    <span class="mdi mdi-check" v-if="trainPass.departureStatus === 'ON_STATION' && !departureChanged"></span>
                    <span class="mdi mdi-alert-circle-outline" v-if="trainPass.departureStatus === 'ON_STATION' && departureChanged"></span>
                    <span class="mdi mdi-clock-check-outline" v-if="trainPass.departureStatus === 'INCOMING' && !departureChanged"></span>
                    <span class="mdi mdi-clock-alert-outline" v-if="trainPass.departureStatus === 'INCOMING' && departureChanged"></span>
                </span>
                {{ trainPass.product.categoryCode }}
            </span>
            <b>{{ trainPass.direction }}</b>
        </p>
        <p>
            <b>{{ trainPass.product.number }}</b>, Spoor {{ trainPass.plannedTrack }}
        </p>
        <p class="priorityAlert" v-if="trainPass.cancelled">
            <span class="mdi mdi-alert-rhombus"></span>
            Deze trein rijdt niet
        </p>
        <!-- <p>
            <span class="mdi mdi-radar"></span>
            Spoor 
            <b>{{ trainPass.plannedTrack }}</b>
        </p>
        <p v-if="trainPass.direction">
            <span class="mdi mdi-arrow-collapse-right"></span>
            Richting 
            <b>{{ trainPass.direction }}</b>
        </p>
        <p v-if="trainPass.origin">
            <span class="mdi mdi-arrow-expand-right"></span>
            Begin punt
            <b>{{ trainPass.origin }}</b>
        </p> -->
        <moreInfoDropDown>

        </moreInfoDropDown>
    </div>
</template>

<script>
import moreInfoDropDown from "@components/moreInfoDropDown.vue";

export default {
    props: ["trainPass"],
    components: {
        moreInfoDropDown
    },
    data() {
        return {
            dropDownOpen: false
        }
    },
    computed: {
        departureChanged() {
            return this.showDepartureTime(this.trainPass.plannedDateTime) !== this.showDepartureTime(this.trainPass.actualDateTime);
        }
    },
    methods: {
        showDepartureTime(time) {
            var date = new Date(time);

            var fixedLengthNumber = number => {
                if (number < 10) {
                    return "0" + number;
                } else {
                    return number;
                }
            };

            return (
                fixedLengthNumber(date.getHours()) +
                ":" +
                fixedLengthNumber(date.getMinutes())
            );
        },
        toFullName(shortName) {
            return shortName === "SPR" ? "Sprinter" : "Intercity";
        }
    }
}
</script>

<style lang="scss">
@import "../../scss/config.scss";

.departure {
    width: calc(95% - 32px);

    padding: 4px 16px;
    margin: 0px auto;

    p {
        margin: 8px;

        font-family: $pFont;

        &.priorityAlert {
            color: #ff3030;
            font-weight: 700;
            text-align: center;
        }

        .plannedDateTime {
            color: #008607;
            font-weight: 700;
        }

        .changedTime {
            color: #ff3030;
            font-weight: 700;
        }

        .oldTime {
            text-decoration: line-through;
        }

        .lineProduct {
            margin-right: 8px;
            padding: 2px 8px;

            border-radius: 5px;

            &.IC {
                color: #000000;
                background-color: #ffe600;
            }

            &.SPR {
                color: #ffffff;
                background-color: #2196f3;
            }
        }

        .expectedTime {
            text-decoration: line-through;
        }

        .actualTime {
            font-weight: 700;
            color: #ff3030;
        }

        .busLine {
            padding: 2px 8px;
            border-radius: 5px;
        }

        &.startEnd {
            text-align: center;

            &.emptyMiddle {
                text-align: left;
            }

            .left {
                float: left;
            }

            .right {
                float: right;

                .mdi + .mdi {
                    margin-left: 4px;
                }
            }

            &.shiftUp {
                margin-top: -36px;
                margin-bottom: 54px;
            }
        }
    }
}
</style>