import { db } from "./config.js";

const cachePrefix = "fetch";

function cacheCleanup() {
    var dbContent = db.getAll({});

    dbContent = Object.entries(dbContent);
    dbContent = dbContent.filter(([key, value]) => typeof value === "object" && value.maxAge !== undefined);
    dbContent.forEach(([key, { cachedAt, maxAge }]) => {
        var now = Date.now();
        var difference = now - cachedAt;

        if (difference > maxAge) {
            db[key] = null;
        }
    });
}

export default async function cachedCall(endpoint, settings) {
    var { query = {}, maxAge = 30000 } = settings || {};
    var finalEndpoint = endpoint;
    var entries = Object.entries(query);
    var functionResponse;

    if (entries.length > 0) {
        for (var i = 0; i < entries.length; i++) {
            let entry = entries[i];

            finalEndpoint += i === 0 ? "?" : "&";
            finalEndpoint += `${entry[0]}=${entry[1]}`;
        }
    }

    var cacheLocationName = cachePrefix + finalEndpoint;

    if (db[cacheLocationName]) {
        var { cachedAt, content } = db[cacheLocationName];
        var now = Date.now();
        var difference = now - cachedAt;

        if (difference <= maxAge) {
            functionResponse = content;
        }
    }

    if (!functionResponse) {
        var fetchResponse = await fetch(finalEndpoint).then(r => r.json());

        db[cacheLocationName] = {
            content: fetchResponse,
            cachedAt: Date.now(),
            maxAge
        }

        setTimeout(() => {
            db[cacheLocationName] = null;

            cacheCleanup();
        }, maxAge + 5000)

        functionResponse = fetchResponse;
    }

    cacheCleanup();

    return functionResponse;
}