<template>
    <section class="dynamicInfo" v-if="nearestStaionLoaded">
        <h1>Station {{ nearestStation.namen.lang }}</h1>
        <trainPass v-for="trainPass in trainPasses" :key="trainPass.id" :trainPass="trainPass" />
    </section>
</template>

<script>
import vueChannel from "vue-channel";
import genId from "genid";

import trainPass from "./trainPass.vue";

import stationsQueryable from "@json/stationsSearchable.json";

import serverCall from "@js/serverCall.js";

var timer;

export default {
    components: {
        trainPass
    },
    data() {
        return {
            pos: {
                lat: 0,
                long: 0
            },
            trainPasses: [],
            nearestStation: {},
            nearestStaionLoaded: false,
            busCode: false
        }
    },
    methods: {
        dynamicInfo() {
            if (this.pos.lat !== 0 && this.pos.long !== 0) {
                var stationDistances = stationsQueryable.map(station => {
                    var distanceLat = this.pos.lat - station.lat;
                    var distanceLong = this.pos.long - station.lng;

                    var directDistance = Math.sqrt(distanceLat ** 2 + distanceLong ** 2);

                    station.directDistance = directDistance;

                    return station;
                });

                stationDistances = stationDistances.sort((a, b) => a.directDistance - b.directDistance);

                var closestStation = stationDistances[0];

                this.nearestStation = Object.assign({}, this.nearestStation, closestStation);
                this.nearestStaionLoaded = true;

                var stationCode = closestStation.code;

                serverCall("/departures/" + stationCode).then(response => {
                    response = response.payload.departures;

                    var trainPasses = response.map(
                        pass => {
                            return {
                                ...pass,
                                id: genId(9)
                            };
                        }
                    );

                    this.trainPasses = trainPasses;
                });

                return true;
            } else {
                return false;
            }
        },
        asScheduled(departure) {
            return departure.actualDateTime === departure.plannedDateTime;
        },
    },
    created() {
        vueChannel("pos").receive(state => {
            this.pos.lat = state.latitude;
            this.pos.long = state.longitude;
        });

        timer = setInterval(() => {
            this.dynamicInfo();
        }, 15000);

        const tryDynamicInfo = i => {
            if (i < 10) {
                var foundNearest = this.dynamicInfo();

                if (!foundNearest) {
                    setTimeout(() => {
                        tryDynamicInfo(i + 1);
                    }, 1000);
                }
            }
        }

        tryDynamicInfo(0);
    },
    destroyed() {
        clearInterval(timer);
    }
}
</script>

<style lang="scss" scoped>

@import "../../scss/config.scss";

h1 {
    margin: 16px 0;

    text-align: center;
    font-family: $hFont;
    font-weight: 400;
    color: #000000;
}

</style>