<template>
    <div
        class="routeStep"
        :class="tripStepPartClasses(step)"
        :style="{ borderLeftColor: tripStepBusColor(step) }"
    >
        <div v-if="step.travel_mode === 'WALKING'">
            <p>
                <span class="mdi mdi-walk"></span>
                {{ toDurationFormat(step.duration.value) }}
            </p>
        </div>
        <div v-if="step.travel_mode === 'TRANSFER'">
            <p>
                <span class="mdi mdi-swap-horizontal"></span>
                {{ toDurationFormat(step.duration.value) }}
            </p>
        </div>
        <div v-if="step.travel_mode === 'TRANSIT'">
            <div v-if="isBus || isTrain">

                <!-- General transit information -->

                <p class="startEnd">
                    <span
                        class="busLine"
                        :style="{
                            backgroundColor: stepBackgroundColor(),
                            color: stepForegroundColor()
                        }"
                    >
                        <span v-if="isBus" class="mdi mdi-bus"></span>
                        <span v-if="isTrain" class="mdi mdi-train"></span>
                        {{ step.transit_details.line.short_name }}
                    </span>
                </p>
                <p class="startEnd shiftUp">
                    <span class="left">
                        <span v-if="departureChanged">
                            <span class="actualTime">{{ toStringTime(time.departureStart.actual) }}</span>
                            -
                            <span class="expectedTime">{{ toStringTime(time.departureStart.expected) }}</span>
                        </span>
                        <span v-if="!departureChanged">{{ toStringTime(time.departureStart.actual) }}</span>
                    </span>
                    <span class="right">{{ toStringTime(time.arrivalFinish.actual) }}</span>
                </p>

                <!-- Trans specific information -->

                <div v-if="isTrain">
                    <div v-if="!trainCancelled">
                        <p class="startEnd emptyMiddle">
                            Spoor {{ platformInfo.departure }}
                            
                            <span class="mdi mdi-arrow-expand-right"></span>
                            <span class="right">{{ step.transit_details.departure_stop.name }}</span>
                        </p>
                        <p class="startEnd emptyMiddle">
                            Spoor {{ platformInfo.arrival }}
                            
                            <span class="mdi mdi-arrow-collapse-right"></span>
                            <span class="right">{{ step.transit_details.arrival_stop.name }}</span>
                        </p>
                    </div>
                    <div v-else>
                        <p class="priorityAlert">
                            <span class="mdi mdi-alert-rhombus"></span>
                            Deze trein rijdt niet
                        </p>
                    </div>
                </div>

                <!-- Additional information -->

                <div v-if="(additionalDataAvailable || livaDataAvailable) && !trainCancelled">
                    <p class="startEnd" @click="dropDownOpen = !dropDownOpen">
                        <span v-if="!dropDownOpen">
                            <span class="mdi mdi-chevron-down"></span>
                            meer info
                        </span>
                        <span v-else>
                            <span class="mdi mdi-chevron-up"></span>
                            minder info
                        </span>
                    </p>
                    <div v-if="dropDownOpen">
                        <div v-if="additionalDataAvailable">

                            <!-- Bus specific additional information -->

                            <div v-if="isBus">
                                <p>
                                    <span class="mdi mdi-arrow-expand-right"></span>
                                    {{ step.transit_details.departure_stop.name }}
                                </p>
                                <p>
                                    <span class="mdi mdi-arrow-collapse-right"></span>
                                    {{ step.transit_details.arrival_stop.name }}
                                </p>
                                <p class="startEnd emptyMiddle">
                                    <span class="mdi mdi-arrow-left-right"></span>
                                    {{ additionalData.LineName }}
                                </p>
                                <p>
                                    <span class="mdi mdi-progress-wrench"></span>
                                    {{ additionalData.OperatorCode }}
                                </p>
                                <p v-if="liveMessage.length > 0">
                                    <span class="mdi mdi-access-point"></span>
                                    {{ liveMessage }}
                                </p>
                                <div v-if="intermediaryStops.length > 0">
                                    <h6>
                                        <span class="mdi mdi-bus-stop"></span>
                                        Planned stops
                                    </h6>
                                    <div v-for="stop in intermediaryStops" :key="stop.id" class="stop" :class="[ stop.stopStatus ]">
                                        <p>
                                            {{ stop.TimingPointName }}
                                        </p>
                                    </div>
                                </div>
                                <p class="refreshToken">
                                    {{ refreshToken }}
                                </p>
                            </div>

                            <!-- Trains specific information -->

                            <div v-if="isTrain">
                                <div class="trainImages">
                                    <img :src="trainPart.afbeelding" alt="Deel" v-for="trainPart in provideId(additionalData.materieeldelen)" :key="trainPart.id" />
                                </div>
                                <p class="startEnd emptyMiddle">
                                    <span class="mdi mdi-arrow-left-right"></span>
                                    <span></span>
                                    <span class="right">
                                        <b>{{ step.transit_details.line.name }}</b>
                                    </span>
                                </p>
                                <!-- <p>
                                    
                                    {{ step.transit_details.line.name }}
                                </p> -->
                                <p class="startEnd emptyMiddle">
                                    <span class="mdi mdi-counter"></span>
                                    ritnummer
                                    <span class="right">
                                        <b>{{ id }}</b>
                                    </span>
                                </p>
                                <p class="startEnd emptyMiddle">
                                    <span class="mdi mdi-tape-measure"></span>
                                    lengte
                                    <span class="right">
                                        <b>{{ additionalData.lengte }}</b> delen
                                    </span>
                                </p>
                                <p class="startEnd emptyMiddle" v-for="materialPart in additionalData.materieeldelen" :key="materialPart.materieelnummer">
                                    <span class="mdi mdi-barcode"></span>
                                    treinstel <b>{{ materialPart.materieelnummer }}</b>
                                    <span class="right">
                                        <span class="mdi" :class="facilityIcon(facility)" v-for="facility in materialPart.faciliteiten" :key="facility + materialPart.materieelnummer"></span>
                                    </span>
                                </p>
                                <p v-if="unspecifiedPlatform">
                                    <span class="mdi mdi-alert-circle-outline"></span>
                                    nog niet alle data is beschikbaar
                                </p>
                                <p v-for="trainMessage in provideId(trainMessages)" :key="trainMessage.id">
                                    <b>
                                        <span class="mdi mdi-information-variant"></span>
                                        {{ trainMessage.message }}
                                    </b>
                                </p>
                            </div>
                        </div>
                        <!-- Depending on the context, live information might be shown -->
                        <!-- <liveStepInfo v-if="livaDataAvailable" :step="step" /> -->
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import genId from "genid";

import liveStepInfo from "./liveStepInfo.vue";

import serverCall from "@js/serverCall.js";
import { db } from "@js/config.js";
import {
    tripStepPartClasses,
    toDurationFormat,
    tripStepBusColor,
    toTime,
    findClosestStation,
    findClosestBusStop,
    findPassingBasedOnTime,
    UTCtoISO,
    findTrainDeparture,
    findTrainArrival,
} from "@js/dashboard/shared.js";
import cachedCall from "@js/cachedCall.js";
import provideId from "@js/provideId.js";

var timer;
var liveTimer;
var departureTimer;
var arrivalTimer;

export default {
    props: ["step"],
    components: {
        liveStepInfo
    },
    data() {
        return {
            additionalData: {},
            livaDataAvailable: false,
            additionalDataAvailable: false,
            dropDownOpen: false,
            isTrain: false,
            isIntercity: false,
            isBus: false,
            liveMessage: "",
            time: {
                departureStart: {
                    actual: 0,
                    expected: 0
                },
                arrivalFinish: {
                    actual: 0,
                    expected: 0
                }
            },
            busStop: {},
            firstBusStop: {},
            intermediaryStops: [],
            activateLiveAt: 0,
            refreshToken: "",
            trainCancelled: false,
            trainMessages: [],
            platformInfo: {
                departure: "?",
                arrival: "?"
            },
            id: ""
        }
    },
    watch: {
        step() {
            this.initStep();
        },
        departureInfo() {
            this.getDepartureInfo();
        }
    },
    computed: {
        departureDelta() {
            var { expected, actual } = this.time.departureStart;

            return actual - expected;
        },
        arivalDelta() {
            var { expected, actual } = this.time.arrivalFinish;

            return actual - expected;
        },
        departureChanged() {
            return this.departureDelta > 60000 || this.departureDelta < -60000;
        },
        arrivalChanged() {
            return this.arivalDelta > 60000 || this.arivalDelta < -60000;
        },
        unspecifiedPlatform() {
            return this.platformInfo.departure === "?" || this.platformInfo.arrival === "?"
        }
    },
    methods: {
        tripStepPartClasses,
        toDurationFormat,
        tripStepBusColor,
        toTime,
        provideId,
        getDepartureInfo() {
            var { departure, arrival } = this.platformInfo;

            if (departure === "?") {
                if (db["departure_" + this.id] !== null) {
                    this.platformInfo.departure = db["departure_" + this.id].value;
                }
            } else {
                db["departure_" + this.id] = {
                    maxAge: 3600000,
                    cachedAt: Date.now(),
                    value: departure
                }
            }

            if (arrival === "?") {
                if (db["arrival_" + this.id] !== null) {
                    this.platformInfo.arrival = db["arrival_" + this.id].value;
                }
            } else {
                db["arrival_" + this.id] = {
                    maxAge: 3600000,
                    cachedAt: Date.now(),
                    value: arrival
                }
            }
        },
        toStringTime(timestamp) {
            var time = new Date(timestamp);

            const toFixedLength = nr => nr >= 10 ? nr : "0" + nr;

            return toFixedLength(time.getHours()) + ":" + toFixedLength(time.getMinutes());
        },
        initStep() {
            this.liveMessage = "";

            this.isTrain = tripStepPartClasses(this.step).includes("train");
            this.isBus = tripStepPartClasses(this.step).includes("bus");
            this.isIntercity = tripStepPartClasses(this.step).includes("ic");

            this.getInfo();
            this.initLiveInfo();
        },
        facilityIcon(facility) {
            if (facility === "WIFI") {
                return ["mdi-wifi-strength-4"];
            } else if (facility === "TOILET") {
                return ["mdi-human-male-female"];
            } else if (facility === "STILTE") {
                return ["mdi-volume-mute"];
            } else if (facility === "FIETS") {
                return ["mdi-bicycle"];
            } else if (facility === "STROOM") {
                return ["mdi-power-socket-eu"];
            }
        },
        async getInfo() {
            if (this.isTrain || this.isBus) {
                var departureTime = UTCtoISO(this.step.transit_details.departure_time.value);
                var arrivalTime = UTCtoISO(this.step.transit_details.arrival_time.value);

                this.time.departureStart.actual = departureTime;
                this.time.departureStart.expected = departureTime;
                this.time.arrivalFinish.actual = arrivalTime;
                this.time.arrivalFinish.expected = arrivalTime;
            }
        },
        stepBackgroundColor() {
            if (this.isBus) {
                return this.step.transit_details.line.color;
            } else if (this.isTrain) {
                return this.isIntercity ? "#ffe600" : "#2196f3";
            }
        },
        stepForegroundColor() {
            if (this.isBus) {
                return this.step.transit_details.line.text_color;
            } else if (this.isTrain) {
                return this.isIntercity ? "#000000" : "#ffffff";
            }
        },
        async initLiveInfo(retryIteration = 0) {
            var { id, transit_details } = this.step;
            var transitDetails = Object.assign({}, transit_details);

            if (liveTimer) clearInterval(liveTimer)

            if (this.isBus) {
                var departureStop = transitDetails.departure_stop.location;
                var arrivalStop = transitDetails.arrival_stop.location;

                // Requests the required information about stop and journey
                var busStop = await findClosestBusStop(departureStop.lat, departureStop.lng);
                var finalStop = await findClosestBusStop(arrivalStop.lat, arrivalStop.lng);
                var pass = findPassingBasedOnTime(busStop.Passes, UTCtoISO(transitDetails.departure_time.value));

                var closestDelta = pass.deltaTime;

                this.busStop = Object.assign({}, this.busStop, busStop);
                this.additionalDataAvailable = true;

                // Checks if the jouney could be found using the 'time' strategy
                if (closestDelta >= -900000 && closestDelta <= 900000) {
                    this.id = pass.__id;

                    this.additionalData = Object.assign({}, this.additionalData, pass);

                    this.liveMessage = "Nog geen data beschikbaar";    
                    
                    const triggerLiveReload = async (forceTrigger = false) => {
                        var busStop = await findClosestBusStop(departureStop.lat, departureStop.lng, 60000);
                        
                        if (Array.isArray(busStop)) {
                            if (liveTimer) clearInterval(liveTimer);
                        } else {
                            var pass = findPassingBasedOnTime(busStop.Passes, UTCtoISO(transitDetails.departure_time.value));

                            // var expectedDepartureTime = new Date(pass.TargetDepartureTime);
                            var actualDepartureTime = new Date(pass.ExpectedDepartureTime);

                            // this.time.departureStart.expected = expectedDepartureTime.getTime();
                            this.time.departureStart.actual = actualDepartureTime.getTime();

                            if (this.dropDownOpen || forceTrigger) {
                                var busPasses = await serverCall("/busJourney/" + pass.__id, { maxAge: 15000 });
                                var stops = Object.values(busPasses.Stops);
                                var nextStop;

                                this.refreshToken = genId(6);

                                if (stops.length === 0) {
                                    if (liveTimer) clearInterval(liveTimer);
                                } else {
                                    stops = stops.map(stop => {
                                        stop.id = genId(16);
                                        return stop;
                                    });

                                    stops.forEach(stop => {
                                        if (!nextStop) {
                                            var status = stop.TripStopStatus;

                                            if (status === "DRIVING" || status === "ARRIVED") {
                                                nextStop = stop;
                                            }
                                        }
                                    });

                                    if (nextStop) {
                                        var status = nextStop.TripStopStatus;

                                        if (status === "DRIVING") {
                                            this.liveMessage = `Rijd naar ${nextStop.TimingPointName}`;
                                        } else if (status === "ARRIVED") {
                                            this.liveMessage = `Aangekomen op ${nextStop.TimingPointName}`;
                                        }

                                        var shownStops = [];
                                        var passedStops = stops.filter(stop => stop.TripStopStatus === "PASSED");
                                        
                                        if (passedStops.length > 0) {
                                            shownStops = [
                                                ...shownStops,
                                                {
                                                    ...passedStops[passedStops.length - 1],
                                                    stopStatus: "passed"
                                                }
                                            ]
                                        }
                                        
                                        shownStops = [
                                            ...shownStops,
                                            {
                                                ...nextStop,
                                                stopStatus: "current"
                                            }
                                        ]

                                        var plannedStops = stops.filter(stop => stop.TripStopStatus === "DRIVING");

                                        if (plannedStops.length > 0) {
                                            plannedStops = plannedStops.slice(1, 3).map(stop => {
                                                var stopStatus;
                                                var { TimingPointName } = stop;

                                                if (TimingPointName === busStop.TimingPointName) {
                                                    stopStatus = "startStop";
                                                } else if (TimingPointName === finalStop.TimingPointName) {
                                                    stopStatus = "finalStop";
                                                } else {
                                                    stopStatus = "planned";
                                                }

                                                return {
                                                    ...stop,
                                                    stopStatus
                                                }
                                            });

                                            shownStops = [
                                                ...shownStops,
                                                ...plannedStops
                                            ]
                                        }

                                        this.intermediaryStops = shownStops;
                                    }
                                }
                            }
                        }
                    }

                    triggerLiveReload(true);

                    liveTimer = setInterval(triggerLiveReload, 15000);
                } else {
                    // Retries when the bus should start it's journey
                    this.liveMessage = "Geen data beschikbaar";

                    if (retryIteration < 5) {
                        retryIteration++;
                        setTimeout(this.initLiveInfo(retryIteration), closestDelta - 300000);
                    }
                }
            } else if (this.isTrain) {
                var data = await serverCall("/virtualTrain/" + transitDetails.trip_short_name);

                this.additionalData = Object.assign({}, this.additionalData, data);
                this.additionalDataAvailable = true;
                this.trainCancelled = false;

                var departureStop = transitDetails.departure_stop;
                var departureLocation = departureStop.location;

                var arrivalStop = transitDetails.arrival_stop;
                var arrivalLocation = arrivalStop.location;

                this.id = transitDetails.trip_short_name;

                var departureStation = findClosestStation(departureLocation.lat, departureLocation.lng);
                var arrivalStation = findClosestStation(arrivalLocation.lat, arrivalLocation.lng);

                var arrivalCheckFunction = async () => {
                    var arrival;

                    if (this.platformInfo.arrival === "?") {
                        arrival = await findTrainArrival(
                            arrivalStation.stationCode,
                            this.id, // Train code
                            this.time.arrivalFinish.expected
                        );

                        if (arrival) {
                            this.platformInfo.arrival = arrival.plannedTrack;
                        }

                        this.getDepartureInfo();
                    } else {
                        clearInterval(arrivalTimer);
                    }
                }

                var departureCheckFunction = async () => {
                    var departure;

                    if (this.platformInfo.departure === "?") {
                        departure = await findTrainArrival(
                            departureStation.stationCode,
                            this.id, // Train code
                            this.time.departureStart.expected
                        );

                        if (departure) {
                            this.trainCancelled = departure.cancelled;
                            this.platformInfo.departure = departure.plannedTrack;

                            if (departure.messages) {
                                this.trainMessages = departure.messages;
                            }
                        }

                        this.getDepartureInfo();
                    } else {
                        clearInterval(departureTimer);
                    }
                }

                departureTimer = setInterval(departureCheckFunction, 300000);
                arrivalTimer = setInterval(arrivalCheckFunction, 300000);

                departureCheckFunction();
                arrivalCheckFunction();
            }
        }
    },
    created() {
        this.initStep();
    },
    destroyed() {
        clearInterval(liveTimer);
        clearInterval(departureStart);
        clearInterval(arrivalTimer);
    }
};
</script>

<style lang="scss" scoped>
@import "../../scss/config.scss";

.routeStep {
    min-height: 16px;
    width: calc(100% - 32px);

    padding: 4px 16px;

    border-left: 2px dotted #9b9b9b;

    .refreshToken {
        text-align: center;
        font-size: 12px;
        color: #cecece;
        margin: 4px;
    }

    &.current {
        width: 100%;

        padding: 4px 0;

        border-left-width: 0;
    }

    .trainImages img {
        max-width: 100%;
        max-height: 64px;
        display: block;
        margin: 0 auto;
    }

    &.transport {
        border-left-style: dashed;

        &.train {
            border-left-style: solid;

            &.ic {
                border-left-color: #cdf321;
            }

            &.spr {
                border-left-color: #2196f3;
            }
        }
    }

    &.end {
        border-left-color: transparent;

        p {
            font-size: 20px;
        }
    }

    h4 {
        margin: 4px 0;

        font-size: 20px;
        font-family: $hFont;
    }

    h6 {
        margin: 16px;

        font-size: 16px;
        font-weight: 700;
        font-family: $hFont;
        text-align: center;
    }

    .stop {
        p {
            margin: 0;
        }

        &.current {
            padding: 8px;
            box-shadow: 0px 2px 9px rgba(0, 0, 0, .2);
            border-radius: 10px;
        }

        &.passed, &.planned, &.startStop, &.finalStop {
            width: 85%;

            margin: 0 auto;
            border-left: 2px dashed #cecece;
            padding: 8px;

            opacity: 0.7;

            &.startStop {
                border-top: 2px dashed #00ff40;
            }

            &.finalStop {
                border-bottom: 2px dashed #ff0000;
            }
        }
    }

    p {
        font-size: 16px;
        font-family: $pFont;

        .expectedTime {
            text-decoration: line-through;
        }

        &.priorityAlert {
            color: #ff3030;
            font-weight: 700;
            text-align: center;
        }

        .actualTime {
            font-weight: 700;
            color: #ff3030;
        }

        .busLine {
            padding: 2px 8px;
            border-radius: 5px;
        }

        &.startEnd {
            text-align: center;

            &.emptyMiddle {
                text-align: left;
            }

            .left {
                float: left;
            }

            .right {
                float: right;

                .mdi + .mdi {
                    margin-left: 4px;
                }
            }

            &.shiftUp {
                margin-top: -36px;
                margin-bottom: 54px;
            }
        }
    }
}
</style>