import genId from "genid";

const isTrainTransit = step => {
    return step.travel_mode === "TRANSIT" && step.transit_details.line.vehicle.name.toLowerCase() === "train";
}

const routeStepsResultMapping = (step, index, array) => {
    step.id = Math.random();

    step.info = step.html_instructions;

    step.info = step.info.replace("Walk to ", "");
    step.info = step.info.replace("Bus towards ", "");
    step.info = step.info.replace("Train towards ", "");

    step.open = false;

    var betweenSteps = array[index - 1] !== undefined && array[index + 1] !== undefined;
    var betweenTrains = false;

    if (betweenSteps) {
        betweenTrains = isTrainTransit(array[index - 1]) && isTrainTransit(array[index + 1]);
    }

    if (betweenSteps && betweenTrains) {
        step.travel_mode = "TRANSFER";
    }

    step.id = genId(16);

    return step;
}

const routesFinalMapping = route => {
    route.id = Math.random();

    route.directions = route.legs[0];

    var startName = route.directions.start_address.split(", ");
    startName.pop();
    startName.pop();
    startName = startName.join(", ");

    route.directions.startName = startName;

    var endName = route.directions.end_address.split(", ");
    endName.pop();
    endName.pop();
    endName = endName.join(", ");

    route.directions.endName = endName;

    route.directions.steps = route.directions.steps
        .filter((step, index, array) => {
            var betweenSteps = array[index - 1] !== undefined && array[index + 1] !== undefined;
            var betweenTrains = false;

            if (betweenSteps) {
                betweenTrains = isTrainTransit(array[index - 1]) && isTrainTransit(array[index + 1]);
            }

            if (betweenSteps && betweenTrains) {
                return true;
            } else if (step.travel_mode === "WALKING" && step.duration.value <= 120) {
                route.directions.duration.value -= step.duration.value;

                return false;
            }

            return true;
        })
        .map(routeStepsResultMapping);

    var totalForCalculation = route.directions.steps
        .map(step => step.duration.value)
        .reduce((a, b) => a + b);

    route.directions.totalForCalculation = totalForCalculation;

    return route;
}

export {
    routeStepsResultMapping,
    routesFinalMapping
}