import stationsLocation from "../../json/stationsSearchable.json";
import { baseEndpoint } from "../../js/config.js";
import cachedCall from "../../js/cachedCall.js";
import serverCall from "../../js/serverCall.js";

const tripStepPartClasses = step => {
    var classes = ["step"];

    if (step.travel_mode === "TRANSIT") {
        var lineInfo = step.transit_details.line;

        classes.push("transport");

        var transportType = lineInfo.vehicle.name.toLowerCase();

        if (transportType === "train") {
            classes.push("train");

            if (lineInfo.short_name === "Intercity") {
                classes.push("ic");
            } else {
                classes.push("spr");
            }
        } else {
            classes.push(transportType);
        }
    }

    return classes;
}

const toTime = timestamp => {
    var timeDate = new Date(0);
    timeDate.setUTCSeconds(timestamp);

    var minutes = timeDate.getMinutes();

    if (minutes < 10) {
        minutes = "0" + minutes;
    }

    return timeDate.getHours() + ":" + minutes;
}

const tripStepIcon = step => {
    var classes = ["mdi"];

    if (step.travel_mode === "WALKING") {
        classes.push("mdi-walk");
    } else if (step.travel_mode === "TRANSFER") {
        classes.push("mdi-swap-horizontal");
    } else {
        var lineInfo = step.transit_details.line;

        classes.push("mdi-" + lineInfo.vehicle.name.toLowerCase());
    }

    return classes;
}

const tripStepBusColor = step => {
    if (step.travel_mode === "TRANSIT" && step.transit_details.line.vehicle.name === "Bus") {
        return step.transit_details.line.color;
    }

    return null;
}

const toDurationFormat = seconds => {
    var minutes = Math.round(seconds / 60);
    var unit;

    if (minutes > 1) {
        unit = "minuten";
    } else {
        unit = "minuut";
    }

    return minutes + " " + unit;
}

const findClosestStation = (lat, long) => {
    var distances = stationsLocation.map(station => {
        var distanceLat = lat - station.lat;
        var distanceLong = long - station.lng;

        var directDistance = Math.sqrt(distanceLat ** 2 + distanceLong ** 2);

        station.directDistance = directDistance;

        return station;
    });

    var distancesSorted = distances.sort((a, b) => a.directDistance - b.directDistance);

    return distancesSorted[0];
}

const findClosestBusStop = (lat, long, maxAge = 30000) => {
    return new Promise(async (res, rej) => {
        var stops = await cachedCall(baseEndpoint + "/busStop", { query: { latLong: `${lat},${long}` }, maxAge });

        res(stops.busStops[0]);
    });
}

const toIdArray = object => {
    var entries = Object.entries(object);

    return entries.map(entry => {
        entry[1].__id = entry[0];
        
        return entry[1];
    });
}

const findPassingBasedOnTime = (passes, timestamp) => {
    var passes = toIdArray(passes);

    var passesDeltaMap = passes.map(pass => {
        var passTimestamp = new Date(pass.ExpectedDepartureTime);
        passTimestamp = passTimestamp.getTime();

        var deltaTime = timestamp - passTimestamp;
        pass.deltaTime = Math.abs(deltaTime);

        return pass;
    });

    var passesSorted = passesDeltaMap.sort((a, b) => a.deltaTime - b.deltaTime);

    return passesSorted[0];
}

const UTCtoISO = epochTime => {
    var date = new Date(0);
    date.setUTCSeconds(epochTime);

    return date.getTime();
}

const findTrainDeparture = async (stationCode, trainCode, timestamp) => {
    var searchTime = new Date(timestamp - 120000);
    var departures = await serverCall("/departures/" + stationCode, { 
        query: {
            dateTime: searchTime.toISOString()
        },
        maxAge: 120000
    });

    departures = departures.payload.departures;

    var trainDeparture = departures.filter(departure => {
        return departure.product.number == trainCode;
    });

    return trainDeparture[0];
}

const findTrainArrival = async (stationCode, trainCode, timestamp) => {
    var searchTime = new Date(timestamp - 120000);
    var departures = await serverCall("/arrivals/" + stationCode, {
        query: {
            dateTime: searchTime.toISOString()
        },
        maxAge: 120000
    });

    departures = departures.payload.arrivals;

    var trainDeparture = departures.filter(departure => {
        return departure.product.number == trainCode;
    });

    return trainDeparture[0];
}

export {
    tripStepPartClasses,
    toTime,
    tripStepIcon,
    tripStepBusColor,
    toDurationFormat,
    findClosestStation,
    findClosestBusStop,
    findPassingBasedOnTime,
    UTCtoISO,
    findTrainDeparture,
    findTrainArrival,
}